*{margin:0;padding:0;box-sizing:border-box}body{background-color:#212121;overflow-x:hidden;height:100vh}h1,h2,h3,h4,h5{color:white;font-family:"Quicksand",sans-serif}p,a{font-family:"Roboto",sans-serif;color:white}.app{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.search__container{height:20vh;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin:0 .5rem}.name__wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;width:auto;overflow-wrap:"break-word"}

h1,h2,h3,h4,h5{color:white;font-family:"Quicksand",sans-serif}p,a{font-family:"Roboto",sans-serif;color:white}.nav{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:100%;height:10vh;background-color:#191919}.nav__logo{padding-left:2rem;cursor:pointer}.nav ul{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.nav li{list-style:none;padding:.5rem;margin:2rem;color:white}.nav a{text-decoration:none;color:unset}.nav h1{font-size:1em;font-weight:200}

h1,h2,h3,h4,h5{color:white;font-family:"Quicksand",sans-serif}p,a{font-family:"Roboto",sans-serif;color:white}.input__wrapper{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;box-shadow:rgba(0,0,0,0.188235) 1px 1px 12px 12px;background-color:#494949;padding:2rem;height:2rem;border-radius:10rem;transition:width 300ms cubic-bezier(0.645, 0.045, 0.355, 1)}.searchbar{font-size:14px;line-height:1;background-color:transparent;width:100%;border:none;color:white;transition:margin 300ms cubic-bezier(0.645, 0.045, 0.355, 1)}.searchbar:focus,.searchbar:active{outline:none}.searchbar::-webkit-input-placeholder{color:white}.searchbar:-ms-input-placeholder{color:white}.searchbar::placeholder{color:white}.search__btn{line-height:1;background-color:transparent;border:none;outline:none;color:white}

h1,h2,h3,h4,h5{color:white;font-family:"Quicksand",sans-serif}p,a{font-family:"Roboto",sans-serif;color:white}.Namelist{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:2rem;width:80%;height:50vh;background-color:#494949;box-shadow:rgba(0,0,0,0.188235) 1px 1px 12px 12px;border-radius:15px}

h1,h2,h3,h4,h5{color:white;font-family:"Quicksand",sans-serif}p,a{font-family:"Roboto",sans-serif;color:white}.card{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;min-width:75px;padding:10px 15px;margin:10px 15px;height:50px;border-radius:20px;font-size:medium}.card:hover{box-shadow:rgba(0,0,0,0.188235) 1px 1px 12px 12px;cursor:pointer}.card:focus{box-shadow:rgba(0,0,0,0.188235) 1px 1px 12px 12px;cursor:pointer}.nameLink{text-decoration:none;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

